<template>
  <div>
    <h1>{{i18n.UpgradingfromVersion4.UpgradingfromVersion4}}</h1>
    <p>{{i18n.UpgradingfromVersion4.Areyouusing}}</p>
    <el-divider></el-divider>
    <h1>Upgrading Quickly and Easily with Kits</h1>
    <p>Our Kits come with version 4 compatibility included - which manages the toughest parts of upgrading for you automatically. Create a kit, add its embed code to your project, and well take care of the following:</p>
    <h1>Auto-mapping Old Icon Names</h1>
    <p>Kits version 4 compatibility handles any references to older version icon names or aliases that have been changed.</p>
    <h1>Always Loading Your Kits Version of Font Awesome</h1>
    <p>Your project may still have CSS that references the old font-family name, a common scenario when your project depends on other themes or plugins that load their own, older versions of Font Awesome. If we find any of those, well override them to use the newer version of Font Awesome youve configured. This will also guarantee that any CSS pseudo-element rules youve set use the proper icon.</p>
    <el-divider></el-divider>
    <h1>Whats Changed From Version 4 to 5?</h1>
    <h2>3 Different Icon Styles</h2>
    <p>We introduced a solid, regular, and light style of every icon in Font Awesome. We also separated out brand icons into their own style/category for easier use. We needed a way to reference a particular style when an icon is called in markup. Its still the case that for each icon you want to use, you need to 1) specify the icons name and 2) use the proper prefix. Version 4 just had one prefix  fa. Version 5 has four prefixes to let us set the style of any icon easily:</p>
    <el-table border
              :data="i18n.UpgradingfromVersion4.tableData"
              style="width: 100%;margin-top:0.5rem;">
      <el-table-column prop="NewPrefix"
                       label="New Prefix"
                       width="180">
      </el-table-column>
      <el-table-column prop="IconStyle"
                       label="Icon Style"
                       width="180">
      </el-table-column>
      <el-table-column prop="SVGJSFilename"
                       label="SVG + JS Filename">
      </el-table-column>
      <el-table-column prop="WebFontFilename"
                       label="Web Font Filename">
      </el-table-column>
      <el-table-column prop="Availability"
                       label="Availability">
      </el-table-column>
    </el-table>
    <div v-highlight
         style="width:100%;">
      <pre class="hljs my-pre">
         <code class="html">
            &lt;i class=&quot;fa fa-camera-retro&quot;&gt;&lt;/i&gt;  &lt;!-- version 4&#x27;s syntax --&gt;

            &lt;i class=&quot;fas fa-camera-retro&quot;&gt;&lt;/i&gt; &lt;!-- version 5&#x27;s syntax --&gt;
            &lt;i class=&quot;far fa-camera-retro&quot;&gt;&lt;/i&gt; &lt;!-- example: regular style of camera-retro --&gt;
            &lt;i class=&quot;fal fa-camera-retro&quot;&gt;&lt;/i&gt; &lt;!-- example: light style of camera-retro --&gt;
          </code>
      </pre>
    </div>
    <h1>{{i18n.UpgradingfromVersion4.IconNameChanges}}</h1>
    <p>{{i18n.UpgradingfromVersion4.Weremoved}}</p>
    <h1>{{i18n.UpgradingfromVersion4.OutlineStyleIcons}}</h1>
    <p>{{i18n.UpgradingfromVersion4.Similarly}}</p>
    <el-divider></el-divider>
    <h1>{{i18n.UpgradingfromVersion4.UpgradingManually}}</h1>
    <p>{{i18n.UpgradingfromVersion4.Weremoved}}</p>
    <ul>
      <li v-if="index<3"
